html, body, #root, #app {
    width: 100vw;
    min-height: 100vh;
    margin: 0;
    padding: 0;
    overflow-x: hidden;
    overflow-y: auto;
}


button {
    background: none;
    border: none;
    margin: 0;
    font-family: inherit;
}

button:hover {
    cursor: pointer;
}

button:focus {
    outline: none;
}

input {
    font-family: inherit;
    font-weight: 300;
    text-align: center;
    padding: 0;
}

input:focus {
    outline: none;
}

input[type="text"], input[type="number"] {
    background: none;
    color: inherit;
    border: none;
    border-radius: 3px;
}

#app {
    display: flex;
    flex-flow: column nowrap;
    justify-content: flex-start;
    align-items: center;
    font-family: 'Cairo', sans-serif;
    font-weight: 400;
}

#full-info {
    width: 100vw;
    display: flex;
    flex-flow: column-reverse nowrap;
    justify-content: flex-start;
    align-items: stretch;
}

.mobile-hide {
    display: none;
}

.mobile-show {
    display: flex;
}

#results {
    width: 100%;
    flex-flow: column nowrap;
    justify-content: center;
    align-items: flex-start;
    padding: 1rem;
    background-color: white;
    color: black;
    box-sizing: border-box;
}

#results-title {
    font-size: 1.5rem;
}

#results-list {
    list-style-type: none;
    font-size: 1rem;
}

#error {
    font-size: 1rem;
}

#info {
    width: 100%;
    padding: 1rem;
    box-sizing: border-box;
    display: flex;
    flex-flow: column nowrap;
    justify-content: flex-start;
    align-items: center;
    background-color: #0e0e0b;
    color: white;
    font-size: 1rem;
    overflow-y: hidden;
}

#info > h1 {
    font-weight: 700;
    font-size: 2rem;
    margin: 0;
    margin-bottom: 1rem;
}

#info > h2 {
    font-weight: 400;
    font-size: 1.5rem;
    margin: 0;
}

#info > p {
    font-size: .75rem;
    margin: 0;
    margin-bottom: 1rem;
    margin-right: 1rem;
}

#info > div {
    margin-bottom: 1rem;
}

#duration {
    margin-left: 1rem;
    border: 1px solid white;
    border-radius: 5px;
}

#final, #return {
    width: 80%;
    height: 3rem;
    background-color: #3a725b;
    color: white;
    border: none;
    border-radius: 5px;
}

#final {
    background-color: #aa282a;
}

.invisible {
    display: none;
}

.visible {
    display: flex;
}

#editor {
    width: 100%;
    flex-flow: row wrap;
    justify-content: flex-start;
    align-items: flex-start;
}

.schedule {
    width: 33%;
    min-height: 65vh;
    display: flex;
    flex-flow: column nowrap;
    justify-content: flex-start;
    align-items: center;
}

.schedule-header {
    color: inherit;
    width: 100%;
    margin: 1rem;
    display: flex;
    flex-flow: column nowrap;
    justify-content: flex-start;
    align-items: center;
}

.schedule-title {
    text-align: center;
    font-weight: 400;
    font-size: 1.5rem;
    margin: 0;
    color: white !important;
    white-space: normal;
}

fieldset {
    border: none;
}

.meetings-field {
    padding: 0;
    margin: 1rem 2%;
    height: 100%;
    width: 40%;
    display: flex;
    flex-flow: column nowrap;
    justify-content: space-evenly;
    align-items: center;
}

.meeting {
    margin-bottom: .5rem;
}

.meeting > p {
    color: inherit;
}

.delete-schedule {
    padding: 0;
    width: 24%;
    margin: 1rem 4% 1rem 0;
    align-self: center;
    display: flex;
    flex-flow: column nowrap;
    justify-content: center;
    align-items: flex-end;
}

.delete-schedule-button {
    font-size: 2rem;
    color: white;
}

.delete-meeting {
    margin-left: 1rem;
    background-color: inherit;
}

.add-meeting {
    margin-top: .5rem;
    margin-bottom: 1rem;
    color: white;
    font-weight: 400;
    border-radius: 5px;
    padding: .1rem 1rem;
}

.symbol {
    background-color: white;
    color: inherit;
    position: relative;
    padding: 0;
    display: inline-block;
    border: 1px solid white;
    border-radius: 100%;
    width: 1rem;
    height: 1rem;
}

.symbol > i {
    color: inherit;
    position: absolute;
    line-height: 1rem;
    min-width: 1rem;
    width: 1rem;
    top: calc(50% - .5rem);
    left: calc(50% - .5rem);
    font-size: .5rem;
}

legend {
    color: white;
    font-size: 1.5rem;
    text-align: center;
    margin-bottom: 1rem;
}

.schedule-1 {
    background-color: #aa282a;
    color: #aa282a;
}

.schedule-3 {
    background-color: #3a725b;
    color: #3a725b;
}

.schedule-5 {
    background-color: #856f20;
    color: #856f20;
}

.schedule-4 {
    background-color: #276e40;
    color: #276e40;
}

.schedule-2 {
    background-color: #bd4e19;
    color: #bd4e19;
}

#create-new {
    width: 33%;
    min-height: 65vh;
    background-color: white;
    display: flex;
    flex-flow: row nowrap;
    justify-content: center;
    align-items: center;
}

#add-schedule {
    font-size: 1.5rem;
    border-radius: 100%;
    height: 5rem;
    width: 5rem;
    border: none;
    background-color: #0e0e0b;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
}

.time-pair {
    display: flex;
    flex-flow: row nowrap;
    justify-content: center;
    align-items: center;
    background-color: white;
    color: inherit;
    border: 2px solid white;
    padding: .25rem 1rem;
    box-sizing: border-box;
    border-radius: 5px;
}

.bounds {
    color: inherit;
}

.time-pair > p {
    margin-left: 1rem;
    margin-right: 1rem;
    font-size: 13.333px;
    font-weight: 300;
    margin-top: 0;
    margin-bottom: 0;
}

@media screen and (min-width: 820px) {
    #app {
        flex-flow: row nowrap;
        align-items: flex-start;
    }

    #full-info {
        position: fixed;
        height: 100vh;
        top: 0;
        height: 100%;
        flex-direction: row;
        transition: right 1s;
    }

    .show-bar {
        right: 100vw;
    }
    
    .show-all {
        right: 0;
    }

    .mobile-hide {
        display: flex;
    }
    
    .mobile-show {
        display: flex;
    }

    #results {
        width: calc(100vw - 20rem);
        height: 100%;
    }

    #info {
        position: fixed;
        height: 100%;
        width: 20rem;
        justify-content: space-evenly;
        align-items: flex-start;
        transition: left 1s;
        overflow-y: hidden;
    }

    #final, #return {
        height: 5%;
    }

    .right-side {
        left: calc(100vw - 20rem);
    }
    
    .left-side {
        left: 0;
    }

    .invisible {
        left: calc(100vw - 20rem);
    }
    
    .visible {
        left: 0;
    }

    #editor {
        display: flex;
        position: relative;
        min-height: 100vh;
        width: calc(100vw - 20rem);
        margin-left: 20rem;
        flex-flow: column nowrap;
        align-items: flex-start;
        transition: left 1s; 
    }

    .schedule {
        min-height: 33vh;
        width: 100%;
        flex-flow: row nowrap;
        justify-content: center;
        align-items: flex-start;
    }

    .schedule-header {
        align-self: center;
        width: 24%;
        margin: 1rem 0 1rem 4%;
        justify-content: center;
        align-items: flex-start;
    }

    .schedule-title {
        text-align: left;
        white-space: nowrap;
    }

    #create-new {
        width: 100%;
        min-height: 0;
        height: 33vh;
    }

    #results-title {
        margin-left: 1rem;
    }
    
}
